import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import content from '../content/projects';
import { Grid } from '@material-ui/core';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';

const styles = theme => ({
  root: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing.unit * 2,
    paddingBottom: theme.spacing.unit * 2,
  },
  card: {
    maxWidth: 345,
  },
  media: {
    height: 140,
  },
});

function Projects(props) {
  const { classes } = props;

  return (
    <div>
      <Paper className={classes.root} elevation={1}>
        <Typography variant="headline">
          {content.title}
        </Typography>
        <br />
        <Grid
          container
          spacing={32}
          direction="row"
          justify="center"
          alignItems="center"
        >
          {content.projects.map((pro) => (
            <Grid item>
              <Card raised className={classes.card}>
                <CardActionArea>
                  {/* <CardMedia className={classes.media} image={process.env.PUBLIC_URL + "/projectcontent/Jubilee Registration/1.png"} /> */}
                  <CardMedia className={classes.media} image={pro.photos[0]} />
                  <CardContent>
                    <Typography gutterBottom variant="h5" component="h2">
                      {pro.title}
                    </Typography>
                    <Typography component="p">
                      {pro.description}<br />
                      qwerty uiop lkj hg f ds azx cvbn m qwer tyuio plkj hgfdsa zxcvfbns oenrfo oskf qwerty uiop lkj hg f ds azx cvbn m qwer tyuio plkj hgfdsa zxcvfbns oenrfo oskfqwerty uiop lkj hg f ds azx cvbn m qwer tyuio plkj hgfdsa zxcvfbns oenrfo oskf
                    </Typography>
                  </CardContent>
                </CardActionArea>
                <CardActions>
                  <Button size="small" color="primary">
                    Github
                  </Button>
                  <Button size="small" color="primary">
                    Live
                  </Button>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Paper>
    </div>
  );
}

Projects.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Projects);